*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}
body{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.app{
    background-image: url('./assets/main_bg.PNG');
    background-size: cover;
    background-position: center;
    transition: 0.4s ease;
}
.app.warm{
    background-image: url('./assets/warm_bg.PNG');
    background-size: cover;
    background-position: center;
}
.app.cold{
    background-image: url('./assets/cold_bg.PNG');
    background-size: cover;
    background-position: center;
}
.app.rainy{
    background-image: url('./assets/rainy_bg.PNG');
    background-size: cover;
    background-position: center;
}
main{
    min-height: 100vh;
    padding:25px;
    background-image: linear-gradient(to bottom, rgba(0,0,0,0.2),rgba(0,0,0,0.75));
}
.search-box{
    width:100%;
    margin: 0 0 75px;
}
.search-box .search-bar{
    padding:15px;
    display:block;
    width:100%;
    appearance: none;
    background: none;
    outline:none;
    background-color: rgba(255,255,255,0);
    border-radius: 0px 0px 16px 16px;
    margin-top: 0px;
    box-shadow: 0px 5px rgba(0,0,0,0.2);
    color:#313131;
    font-size:20px;
    transition:0.4s ease;
}
.search-box .search-bar:focus{
    background-color: rgba(255,255,255,0.75);
}
.location-box .location{
    color:#fff;
    font-size:32px;
    font-weight:500;
    text-align: center;
    text-shadow:3px 3px rgba(50,50,70,0.5);
}
.location-box .date{
    color:#fcfcfc;
    text-align: center;
    font-size: 20px;
    font-weight: 300;
    font-style: italic;
    text-shadow:2px 2px rgba(0, 0, 0, 0.5);
    margin-top: 10px;
}
.weather-box{
    text-align: center;
}
.weather-box .temp{
    position: relative;
    display: inline-block;
    margin: 30px auto;
    background-color: rgba(19, 19, 19, 0.2);
    border-radius: 16px;
    padding:15px 25px;
    color:#f5f1f1;
    font-size: 102px;
    font-weight: 900;
    text-shadow: 3px 6px rgba(50,50,70,0.5);
    text-align: center;
    box-shadow: 3px 6px rgba(0,0,0,0.2);
}
.weather-box .weather{
    color:#fff;
    text-shadow:3px 3px 3px rgba(50,50,70,0.5);
    font-size: 48px;
    font-weight:700;
}
